import React from 'react';
import base from '../base';
import axios from 'axios';

import Header from './Header';
import StocksList from './StocksList';
import Categories from './Categories';
import UserCurrency from './UserCurrency';
import Recommendations from './Recommendations';
import { currencies } from './Currencies';
import { isEmpty } from './helpers';


class App extends React.Component {

  constructor(){
    super();
    this.prices = {};
    this.loading = {};
    this.notification = "";
  }

  state = {
    uid: null,
    userData: {},
    prices: {},
    exchangeRates: {},
  };

  componentWillMount(){
    this.ref = base.syncState("userData",
      {
        context: this,
        state: 'userData'
      });

    const exchangeRates = {};
      Object.keys(currencies).forEach((key1) => {
        axios.get(`https://api.fixer.io/latest?base=${key1}`)
          .then(res => {
            const data = res.data["rates"];
            Object.keys(currencies).forEach((key) => {
              const exchangeKey = `${key}to${key1}`;
              if (key === key1) { exchangeRates[exchangeKey] = 1; }
              else {
                if (data[key]) {exchangeRates[exchangeKey] = 1 / parseFloat(data[key])};
                }
              })
                });
          })
            this.setState({ exchangeRates });
  }

  componentDidMount(){
    base.onAuth((user) => {
      if(user){
        this.authHandler(null, { user });
      }
    });
}
 componentWillUpdate(nextProps, nextState){
   this.getPrices(nextState);
 }

  componentWillUnMount(){
    base.removeBinding(this.ref);
  }

  authenticate = (provider) => {
    base.authWithOAuthPopup(provider, this.authHandler)
  };

  logout = () => {
    base.unauth();
    this.setState({ uid: null});
  };

  authHandler = (err, authData) => {
    const uid = authData.user.uid;
    if(err) {
      console.error(err);
      return;
        }
    this.setState({ uid });
      };

  addStock = (stock) => {
    const uid = this.state.uid;
    const userData = {...this.state.userData};
    let isFound = false;
    if (!userData[uid].stocks) { userData[uid].stocks = {}; };
    Object.keys(userData[uid].stocks).forEach(
      key => {
        if ( userData[uid].stocks[key] &&
          userData[uid].stocks[key].name.toUpperCase() === stock.name.toUpperCase() &&
        userData[uid].stocks[key].category === stock.category) {
          isFound = true;
          userData[uid].stocks[key].amount += stock.amount;
        }
      });
      if (!isFound) {
        const timestamp = Date.now();
        userData[uid].stocks[`stock-${timestamp}`] = stock;
      }

    this.setState({ userData })
  };

  updateStock = (key, updatedStock) => {
    const uid = this.state.uid;
    const userData = {...this.state.userData};
    userData[uid].stocks[key] = updatedStock;
    this.setState({ userData });
  };

  removeStock = (key) => {
    const uid = this.state.uid;
    const userData = {...this.state.userData};
    userData[uid].stocks[key] = null;
    this.setState({ userData });
  };

  addCategory = (category) => {
    const uid = this.state.uid;
    const userData = {...this.state.userData };
    if (!userData[uid].categories) { userData[uid].categories = {}; };
    const timestamp = Date.now();
    userData[uid].categories[`category-${timestamp}`] = category;
    this.setState({ userData });
  };

  updateCategory = (key, updatedCategory) => {
    const uid = this.state.uid;
    const userData = {...this.state.userData};
    userData[uid].categories[key] = updatedCategory;
    this.setState({ userData });
  };

  removeCategory = (key) => {
    const uid = this.state.uid;
    const userData = {...this.state.userData};
    userData[uid].categories[key] = null;
    this.setState({ userData });
  };

  updateCurrency = (event) => {
    const uid = this.state.uid;
    const userData = {...this.state.userData}
    userData[uid].currency = event.target.value;
    this.setState({ userData });
  };

  getPrices = (nextState) => {
    const uid = this.state.uid;
    const stocks = (!isEmpty(nextState.userData[uid])) ? nextState.userData[uid].stocks : {};
    if (!isEmpty(stocks)) {
      Object.keys(stocks).forEach(key => {
        const stock = stocks[key];
        const symbol = (stock) ? stock.name.toUpperCase() : "";

        if (stock && symbol.indexOf('CASH') !== -1
         && !nextState.prices.hasOwnProperty(symbol)){
          this.prices[ symbol ] = 1;
          this.setState( { prices: this.prices });
        }
        else if (stock && isNaN(stock.name) && !this.loading[symbol]
         && !nextState.prices.hasOwnProperty(symbol)) {
          this.loading[symbol] = true;
          const URL = `https://www.alphavantage.co/query?function=TIME_SERIES_DAILY&symbol=${symbol}&interval=60min&apikey=RHWJA47WFXINNL5L`;
          axios.get(URL)
            .then(res => {
              if (res.data) {
                if (res.data.hasOwnProperty('Error Message')) {
                  this.notification = <div className="notification">Price of one or more stocks is not available.
                  Probably  https://www.alphavantage.co/ is down</div>;
                  console.log(res.data['Error Message']);
                  this.prices[symbol] = 0;
                  this.setState( { prices: this.prices });
                  this.loading[symbol] = false;
                }
                if (res.data["Meta Data"]) {
                  const key = Object.keys(res.data["Time Series (Daily)"])[0];
                  const price = res.data["Time Series (Daily)"][key]["4. close"];
                  this.prices[symbol] = parseFloat(price);
                  this.setState( { prices: this.prices });
                  this.loading[symbol] = false;
                }
              }
            })
        }
      });
    }
    else return;

  };

  stockPrice = (s) => {
    let price = 0;
    const uid = this.state.uid;
    if (s && !isEmpty(this.state.prices)) {
      Object.keys(this.state.prices).forEach(key => {
        if (key === s.name.toUpperCase()){
          const exchangeKey = (this.state.userData[uid].currency) ?
           `${s.currency}to${this.state.userData[uid].currency}` :
           `${s.currency}to${Object.keys(currencies)[0]}`;
          price = parseInt(1000 * this.state.prices[key] * this.state.exchangeRates[exchangeKey], 10) / 1000;
        }
      });
    }

    return price;
  };

  stockValue = (stock) => {
    return parseInt(100 * this.stockPrice(stock) * stock.amount, 10) / 100;
  };

  calcUserTotal = () => {
    const uid = this.state.uid;
    this.userTotal = 0;
    this.categoriesTotal = {};
    if (!isEmpty(this.state.userData[uid]) && this.state.userData[uid].stocks){
      Object.keys(this.state.userData[uid].stocks).forEach(key => {
        const stock = this.state.userData[uid].stocks[key];
        if (stock){
          const category = stock.category;
          this.userTotal += this.stockValue(stock);
          if (this.categoriesTotal[category]) {
            this.categoriesTotal[category] += this.stockValue(stock);
          }
          else {
           this.categoriesTotal[category] = this.stockValue(stock);
           }
        }
      });
    }
  };

  makeDeal = () => {
    console.log("here!");
  }


  renderLogin(){
    return (
      <nav className="login">
      <p> Sign in to manage your stocks </p>
      <button className="github" onClick={() =>
         this.authenticate('github')}> Log In with Github </button>
         <button className="facebook" onClick={() =>
            this.authenticate('facebook')}> Log In with Facebook </button>
        <button className="google" onClick={() =>
            this.authenticate('google')}> Log In with Google </button>
      </nav>
    )
  }
  render(){
      const logout = <button className="logout" onClick={this.logout}>Log Out</button>
      //check if they are not logged in at all
      if(!this.state.uid){
        return <div>{this.renderLogin()}</div>
      }
      else {
        const uid = this.state.uid;

        if (this.state.userData[uid]) {
          this.calcUserTotal();
        return (
          <div>
          {logout}
          <Header tagline="Stocks Advisor"/>
          <div className="stocks-main">
          <StocksList
            stocks={this.state.userData[uid].stocks}
            categories={this.state.userData[uid].categories}
            currency={this.state.userData[uid].currency}
            removeStock={this.removeStock}
            updateStock={this.updateStock}
            addStock={this.addStock}
            userTotal={this.userTotal}
            stockPrice={this.stockPrice}
            stockValue={this.stockValue}
            notification={this.notification}
          />
          <div>
            <UserCurrency
              updateCurrency={this.updateCurrency}
              stocks={this.state.userData[uid].stocks}
              categories={this.state.userData[uid].categories}
              currency={this.state.userData[uid].currency}
             />

            <Categories
              stocks={this.state.userData[uid].stocks}
              categories={this.state.userData[uid].categories}
              addCategory={this.addCategory}
              removeCategory={this.removeCategory}
              updateCategory={this.updateCategory}
              categoriesTotal={this.categoriesTotal}
              userTotal={this.userTotal}
            />
            <Recommendations
              stocks={this.state.userData[uid].stocks}
              categories={this.state.userData[uid].categories}
              currency={this.state.userData[uid].currency}
              prices={this.state.prices}
              stockPrice={this.stockPrice}
              exchangeRates={this.state.exchangeRates}
              categoriesTotal={this.categoriesTotal}
              userTotal={this.userTotal}
              makeDeal={this.makeDeal}
            />
           </div>

          </div>
          </div>
        )}
      else { return null }
   }
  }
}

export default App;



